    display: none !important;
}

.weekDays-selector input[type=checkbox]+label {
    display: inline-block;
    border-radius: 15px;
    background: #e00808;
    color: #ffffff;
    font-size: 30px;
    height: 60px;
    width: 60px;
    margin-right: 3px;
    line-height: 55px;
    text-align: center;
    cursor: pointer;
}

input[type=text] {
    width: 50%;
    padding: 8px 12px;
    margin: 8px 0;
    background-color: #ffffff;
    color: rgb(201, 7, 1);
    font-size: 30px;
    border-radius: 15px;
}

.weekDays-selector input[type=checkbox]:checked+label {
    background: #2ad105;
    color: #ffffff;
}
</style>
