<template>
    <div class="navbar-menu" id="navbar-menu">
        <div class="navbar-start">
            <div class="navbar-item">
                <form method="get" action="/search">
                    <div class="field has-addons">
                        <div class="dropdown">
                            <input type="text" class="dropdown-input" placeholder="Who are you looking for?" name="query"
                                v-model="query_term" @input="getSearch()" @click="closeDropdown" autocomplete="off">
                            <div class="dropdown-content" v-show="dropdown">
                                <div class="dropdown-item" v-for="(obj, index) in querySet" :key="index"
                                    @mousedown="selectOption(obj)">
                                    {{ obj }}
                                </div>
                            </div>
                        </div>
                        <div class="control">
                            <button class="button is-success test" @click="goToProduct">
                                <span class="icon">
                                    <i class="fas fa-search" style='font-size:30px;'></i>
                                </span>
                            </button>
                        </div>
                    </div>
                </form>
            </div>
        </div>
    </div>
    <br>
    <br>
    <br>
    <div>
        <div v-if="loading">
            <Loading></Loading>
        </div>
        <div v-else>
            <PatientData v-for="patient in patients" :key="patient.ID" :Name="patient.Name" :Dosage="patient.Dosage"
                :Frequency="patient.Frequency" :Phone="patient.Phone" :Medicine="patient.Medicine" :ID="patient.ID">
            </PatientData>
        </div>
    </div>
    <br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br>
</template>

<script>
// @ is an alias to /src
import axios from "axios"
import PatientData from "@/components/PatientData.vue"
import Loading from "@/components/Loading.vue"
export default {
    name: 'HomeView',
    components: {
        PatientData,
        Loading,
    },
    data() {
        return {
            patients: null,
            loading: true,
            dropdown: false,
            query_term: "",
            querySet: [],
        }
    },
    methods: {
        async fetchPatients() {
            let query_term = this.$route.query["query"];
            await axios
                .get("api/filter-patients?query=" + query_term)
                .then(response => {
                    this.patients = response.data;
                    this.loading = false;
                })
                .catch(err => {
                    console.log(err)
                })
        },
        async getSearch() {
            this.dropdown = false;
            if (this.query_term === "") {
                return;
            }
            await axios
                .get("api/filter-patients?query=" + this.query_term)
                .then(response => {
                    this.querySet = []
                    for (let i = 0; i < response.data.length; i++) {
                        this.querySet.push(response.data[i].Name)
                    }
                    if (this.querySet.length > 0) {
                        this.dropdown = true;
                    }
                })
                .catch(err => {
                    console.log(err)
                })
        },
        selectOption(str) {
            this.query_term = str;
            this.dropdown = false;
        },
        closeDropdown() {
            this.dropdown = false;
        }
    },
    mounted() {
        this.fetchPatients();
    }
}
</script>

