<template>
  <div class="box">
    <div style="clear: both">
      <div style="float: left">
        <h1 style="font-size: 35px; font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif"><b>{{ Name }} ‚ù§Ô∏è‚Äçüî•</b>
        </h1>
        <h1 style="font-size: 20px; font-family:'Times New Roman', Times, serif">{{ line1 }}</h1>
        <h1 style="font-size: 20px; font-family:'Times New Roman', Times, serif">{{ line2 }}</h1>
      </div>
      <div style="float: right">
        <button class="button is-success is-light" @click="updatePatient">Update
          Patient</button> <br><br>
        <button class="button is-link is-light" @click="deletePatient">
          Delete Patient &nbsp;
        </button>
      </div>
    </div>
  </div>
  <br><br><br>
</template>

<script>
import axios from 'axios';
import { toast } from 'bulma-toast';
export default {
  name: 'PatientData',
  props: {
    ID: Number,
    Name: String,
    Phone: String,
    Medicine: String,
    Dosage: String,
    Frequency: String,
  },
  data() {
    return {
      line1: "",
      line2: "",
    }
  },
  methods: {
    async deletePatient() {
      toast({
        message: 'Patient Deleted',
        type: 'is-danger',
        dismissible: true,
        pauseOnHover: true,
        duration: 3000,
        position: 'bottom-right',
      });
      let url = "api/patient/" + this.ID
      await axios
        .delete(url)
        .then(response => {
          this.$router.go()
        })
        .catch(err => { console.log(err) })
    },
    async updatePatient() {
      this.$router.push("/update-patient/" + this.ID)
    }
  },
  mounted() {
    this.line1 = this.Name + " has to take " + this.Dosage + " of " + this.Medicine + "."
    let days = "";
    let cur = 0;
    while (true) {
      if (cur === this.Frequency.length) {
        break;
      }
      if (this.Frequency[cur] == 'T') {
        if (cur + 1 < this.Frequency.length) {
          if (this.Frequency[cur + 1] == 'h') {
            days += "Thursday, ";
            cur += 2;
          }
          else {
            days += "Tuesday, ";
            cur += 1;
          }
        }
        else {
          days += "Tuesday, ";
          cur += 1;
        }
      }
      else if (this.Frequency[cur] == 'S') {
        if (cur + 1 <= this.Frequency.length) {
          if (this.Frequency[cur + 1] == 'u') {
            days += "Sunday, ";
            cur += 2;
          }
          else {
            days += "Saturday, ";
            cur += 1;
          }
        }
      }
      else {
        if (this.Frequency[cur] === 'M') {
          days += "Monday, ";
        }
        if (this.Frequency[cur] === 'F') {
          days += "Friday, ";
        }
        if (this.Frequency[cur] === 'W') {
          days += "Wednesday, ";
        }
        cur += 1;
      }
    }
    days = days.slice(0, -1);
    days = days.slice(0, -1);
    this.line2 = "He has to take it on " + days + ".";

  },
}
</script>

