<template>
    <div v-if="loading">
        <Loading />
    </div>
    <div>
        <div>
            <div>
                <label
                    style="font-size: 21px; color: white; font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif">Patient
                    Name:</label>
                <div>
                    <input type="text" v-model="Name" required>
                </div>
            </div>
            <br>
            <div>
                <label
                    style="font-size: 21px; color: white; font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif">Phone
                    (with country code):</label>
                <div>
                    <input type="text" v-model="Phone" required>
                </div>
            </div>
            <br>
            <div>
                <label
                    style="font-size: 21px; color: white; font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif">Medicine:</label>
                <div>
                    <input type="text" v-model="Medicine" required>
                </div>
            </div>
            <br>
            <div>
                <label
                    style="font-size: 21px; color: white; font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif">Dosage:</label>
                <div>
                    <input type="text" v-model="Dosage" required>
                </div>
            </div>
            <br>
            <div>
                <label
                    style="font-size: 21px; color: white; font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif">Frequency:</label>
                <br><br>
                <div class="weekDays-selector">
                    <input type="checkbox" id="weekday-mon" class="weekday" @click="toggleDay(0)" status />
                    <label for="weekday-mon">M</label>
                    <input type="checkbox" id="weekday-tue" class="weekday" @click="toggleDay(1)" />
                    <label for="weekday-tue">T</label>
                    <input type="checkbox" id="weekday-wed" class="weekday" @click="toggleDay(2)" />
                    <label for="weekday-wed">W</label>
                    <input type="checkbox" id="weekday-thu" class="weekday" @click="toggleDay(3)" />
                    <label for="weekday-thu">T</label>
                    <input type="checkbox" id="weekday-fri" class="weekday" @click="toggleDay(4)" />
                    <label for="weekday-fri">F</label>
                    <input type="checkbox" id="weekday-sat" class="weekday" @click="toggleDay(5)" />
                    <label for="weekday-sat">S</label>
                    <input type="checkbox" id="weekday-sun" class="weekday" @click="toggleDay(6)" />
                    <label for="weekday-sun">S</label>
                </div>
            </div>
            <br>
            <button class="button is-success is-large is-rounded" @click="updatePatient">Update</button> &nbsp;
            <button class="button is-danger is-large is-rounded" @click="deletePatient">Delete</button>
            <br><br><br><br><br><br><br><br><br>
        </div>
    </div>
</template>


<script>
import axios from 'axios';
import Loading from "@/components/Loading.vue"
import { toast } from 'bulma-toast';
export default {
    name: "UpdatePatient",
    components: {
        Loading,
    },
    data() {
        return {
            loading: true,
            Name: null,
            Medicine: null,
            Dosage: null,
            Frequency: null,
            Phone: null,
            select: [0, 0, 0, 0, 0, 0, 0],
        }
    },
    methods: {
        async getPatientDetails() {
            const ID = this.$route.params["slug"]
            await axios
                .get("api/patient/" + ID)
                .then(response => {
                    this.Name = response.data["Name"]
                    this.Medicine = response.data["Medicine"]
                    this.Phone = response.data["Phone"]
                    this.Dosage = response.data["Dosage"]
                    this.Frequency = response.data["Frequency"]
                    this.loading = false;
                    let cur = 0;
                    while (true) {
                        if (cur === this.Frequency.length) {
                            break;
                        }
                        if (this.Frequency[cur] == 'T') {
                            if (cur + 1 <= this.Frequency.length) {
                                if (this.Frequency[cur + 1] == 'h') {
                                    this.select[3] = 1;
                                    cur += 2;
                                }
                                else {
                                    this.select[1] = 1;
                                    cur += 1;
                                }
                            }
                            else {
                                this.select[1] = 1;
                                cur += 1;
                            }
                        }
                        else if (this.Frequency[cur] == 'S') {
                            if (cur + 1 <= this.Frequency.length) {
                                if (this.Frequency[cur + 1] == 'Su') {
                                    this.select[6] = 1;
                                    cur += 2;
                                }
                                else {
                                    this.select[5] = 1;
                                    cur += 1;
                                }
                            }
                            else {
                                this.select[5] = 1;
                                cur += 1;
                            }
                        }
                        else {
                            if (this.Frequency[cur] === 'M') {
                                this.select[0] = 1;
                            }
                            if (this.Frequency[cur] === 'F') {
                                this.select[4] = 1;
                            }
                            if (this.Frequency[cur] === 'W') {
                                this.select[2] = 1;
                            }
                            cur += 1;
                        }
                    }
                    let mon = document.getElementById("weekday-mon")
                    mon.checked = this.select[0] === 1;
                    let tue = document.getElementById("weekday-tue")
                    tue.checked = this.select[1] === 1;
                    let wed = document.getElementById("weekday-wed")
                    wed.checked = this.select[2] === 1;
                    let thu = document.getElementById("weekday-thu")
                    thu.checked = this.select[3] === 1;
                    let fri = document.getElementById("weekday-fri")
                    fri.checked = this.select[4] === 1;
                    let sat = document.getElementById("weekday-sat")
                    sat.checked = this.select[5] === 1;
                    let sun = document.getElementById("weekday-sun")
                    sun.checked = this.select[6] === 1;
                    this.loading = false;
                })
                .catch(err => { console.log(err) })
        },
        async updatePatient() {
            toast({
                message: 'Patient Updated',
                type: 'is-warning',
                dismissible: true,
                pauseOnHover: true,
                duration: 3000,
                position: 'bottom-right',
            });
            this.Frequency = [];
            for (var i = 0; i < 7; i++) {
                if (this.select[i] == 1) {
                    if (i == 0) this.Frequency += 'M';
                    if (i == 1) this.Frequency += 'T';
                    if (i == 2) this.Frequency += 'W';
                    if (i == 3) this.Frequency += 'Th';
                    if (i == 4) this.Frequency += 'F';
                    if (i == 5) this.Frequency += 'S';
                    if (i == 6) this.Frequency += 'Su';
                }
            }
            const patientDetails = {
                "Name": this.Name,
                "Medicine": this.Medicine,
                "Dosage": this.Dosage,
                "Frequency": this.Frequency,
                "Phone": this.Phone,
            };
            const ID = this.$route.params["slug"];
            await axios
                .patch("api/patient/" + ID, patientDetails)
                .then(response => {
                    this.$router.push('../')
                })
                .catch(err => { console.log(err) })
        },
        async deletePatient() {
            toast({
                message: 'Patient deleted',
                type: 'is-danger',
                dismissible: true,
                pauseOnHover: true,
                duration: 4000,
                position: 'bottom-right',
            });
            let url = "api/patient/" + this.$route.params["slug"];
            await axios
                .delete(url)
                .then(response => {
                    this.$router.push('../')
                })
                .catch(err => { console.log(err) })
        },
        toggleDay(i) {
            this.select[i] = 1 - this.select[i];
        }
    },
    mounted() {
        this.getPatientDetails();
    }
}
</script>


<style lang="scss" scoped>
.weekDays-selector input {
    display: none !important;
}

.weekDays-selector input[type=checkbox]+label {
    display: inline-block;
    border-radius: 15px;
    background: #e00808;
    color: #ffffff;
    font-size: 30px;
    height: 60px;
    width: 60px;
    margin-right: 3px;
    line-height: 55px;
    text-align: center;
    cursor: pointer;
}

input[type=text] {
    width: 50%;
    padding: 8px 12px;
    margin: 8px 0;
    background-color: #ffffff;
    color: rgb(201, 7, 1);
    font-size: 30px;
    border-radius: 15px;
}

.weekDays-selector input[type=checkbox]:checked+label {
    background: #2ad105;
    color: #ffffff;
}
</style>

